          <van-search background="#F5F6FA" placeholder="请输入搜索关键词" />
          <div class="core">
            <van-icon name="scan" size="30" />
          </div>
        </div>
        <!-- end -->
      </div>
      <!-- end -->

      <!-- 电影区 -->
      <div class="font-moves">
