<script>
import { mapState, mapMutations } from "vuex";
export default {
  name: "home",
  data() {
    return {
      images: [
        "http://img0.imgtn.bdimg.com/it/u=1924022879,4273089581&fm=26&gp=0.jpg",
        "http://img2.imgtn.bdimg.com/it/u=3243324722,4234510990&fm=15&gp=0.jpg",
        "http://img4.imgtn.bdimg.com/it/u=3882197996,1256535102&fm=26&gp=0.jpg",
        "http://img4.imgtn.bdimg.com/it/u=4205087006,425998086&fm=15&gp=0.jpg"
      ],
      result1: "",
      index1: 0,
      index2: 0,
      newmoves: "",
      koubei: "",
      width: 0,
      maxsaw: 0,
      minsaw: 0
    };
  },
  methods: {
    ...mapState({}),
    getmoves(o) {
      this.$router.push(o);
    }
  },

  created() {
    //开启加载提示
    this.$toast.loading({
      duration: 0,
      message: "加载中..."
    });
    //请求热映电影数据
    //  this.$store.commit('newhome');
    this.axios({
      method: "GET",
      url: "https://douban.uieee.com/v2/movie/in_theaters"
    }).then(result => {
      //合并演员导演
      result.data.subjects.forEach(v => {
        v.avatarsInfo = "";
        v.casts.forEach(item => {
          v.avatarsInfo += item.name + "、";
        });
        this.index1 += 1;
        v.avatarsInfo = v.avatarsInfo.slice(0, -1);
        v.directc = "";
        v.directors.forEach(item => {
          v.directc += item.name + "、";
          v.directc = v.directc.slice(0, -1);
        });
      });
      this.result1 = result.data;
        this.$nextTick(() => {
          let listW = this.$refs.listw[0].clientWidth + 30;
          //动态设置ul的宽度
         result.data.Width = listW * result.data.subjects.length;
        });
    });

    //请求新片电影数据
    this.axios({
      methods: "GET",
      url: "https://douban.uieee.com/v2/movie/coming_soon"
    }).then(reche => {
      reche.data.subjects.forEach(v => {
        this.index2 += 1;
      });
      this.newmoves = reche.data.subjects;
      setTimeout(() => {
        //设置ul宽度
        this.$nextTick(() => {
          let kistW = this.$refs.kist[0].clientWidth;
          //动态设置ul的宽度
          this.maxsaw = kistW * (reche.data.subjects.length+2);
        });
      }, 100);
    });
    //口碑榜
    this.axios({
      methods: "GET",
      url:
        "https://douban.uieee.com/v2/movie/weekly?apikey=0df993c66c0c636e29ecbb5344252a4a"
    }).then(koubei => {
      this.koubei = koubei.data.subjects;

      koubei.data.subjects.forEach(v => {
        //合并导演和演员
        v.subject.koul = "";
        v.subject.casts.forEach(item => {
          v.subject.koul += item.name + "、";
        });
        v.subject.koul = v.subject.koul.slice(0, -1);
        v.subject.kou2 = "";
        v.subject.directors.forEach(item => {
          v.subject.kou2 += item.name + "、";
        });
        this.$nextTick(() => {
          //获取星星宽度
          let width = this.$refs.noactivestar[0].clientWidth;
          v.width = (v.subject.rating.average / v.subject.rating.max) * width;
        });
        v.subject.kou2 = v.subject.kou2.slice(0, -1);

        this.$toast.clear();
      });
    });
  }
};
</script>